.footer{
background-color: $color-silhouette-dark;
color:$color-silhouette-light;
color:rgba(255,255,255,.5);
font-size: $small;
padding: 30px 30px;

& hr {
border-top: 1px solid #555;
margin: 30px 0;
}

& h5 {
font-size: $med;
font-family: inherit;
margin-top: 10px;
color:$color-white;
}

& a {
color:$color-pink;
}

& a:hover{
color:$color-purple;
text-decoration:none;
}

.cp{
//color:$color-silhouette-yellow !important;
text-size:$small;
}

& .icons a {
font-size: $med;
color:$color-silhouette-light;
@include transition;
color:$color-silver;
}

& .icons a:hover{
transform: scale(1.2);
}

& > div > div{
& > a.develop{
width:80px;
height:40px;
margin: 0px 10px 0px 10px;
@include responsive(phone){
height:40px;
width:80px;
}
@include responsive(big-tablet){
height:60px;
width:110px;
}

& > .footer-logo{
background-size:cover;
background-position:center center;
background-repeat:no-repeat;
border-radius:$rad-three;
height:100%;
width:100%;
@include transition;
}//end footer logo
}//end develop
& > .develop:hover{
transform: scale(1.2);
}
}//end double div


}//end footer
