//article on list page
.post-summary-grid {
display: grid;
grid-template-columns: 1fr;
grid-template-rows: auto;
border: 1px solid #ccc;
border-radius:$rad-big;
justify-content: center;
margin-bottom:50px;
background-color:#fff;

& h2{
@include responsive(small-phone) {
font-size: 1.5rem;
}
@include responsive(phone) {
font-size: 1.5rem;
}
@include responsive(tablet) {
font-size: 2rem;
}
}//end summary-grid h2

@include responsive(small-phone) {
font-size: .8rem;
}
@include responsive(phone) {
font-size: .8rem;
}
@include responsive(tablet) {
font-size: 1rem;
}

& > .post-summary-image {
grid-column: 1;
grid-row: 1;

@include responsive(small-phone) {
height: 200px;
}
@include responsive(phone) {
height: 200px;
}
@include responsive(tablet) {
height: 200px;
}
@include responsive(desktop) {
height: 250px;
}
@include responsive(big-desktop) {
height: 300px;
}

& > .list-img {
height: 100%;
background-size: cover;
background-repeat: no-repeat;
background-position: center;
padding: -0.2rem;
background-color: #fff;
border-radius:$top-rad-big;
margin: -0.06rem;
}
}//end .post-summary-image

& > .post-summary-title {
grid-column: 1;
grid-row: 3;
width: 100%;
text-align: center;
align-self: center;
margin-top:.8rem;
margin-bottom:.8rem;
}//end post summary title


& > .post-summary-meta {
grid-column: 1;
grid-row: 2;
border: none;
border-radius: none;
}


& > .post-summary {
grid-column: 1;
grid-row: 4;
width: 100%;
justify-self: center;
text-align: center;
padding-left: 0.3rem;
padding-right: 0.3rem;
padding-bottom: 1rem;
border-bottom: 1px solid #ccc;
}

& > .post-summary-continue {
grid-column: 1;
grid-row: 5;
border-left: none;
border-right: none;
border-top: 1px solid #ccc;
border: none;
border-radius:$bottom-rad-big;
z-index: -0;
//class .btn bootstrap
display: inline-block;
font-weight: 400;
color: #212529;
text-align: center;
vertical-align: middle;
cursor: pointer;
user-select: none;
//background-color: transparent;
//border: 1px solid transparent;
padding: .375rem .75rem;
font-size: $normal;
line-height: 1.5;
//border-radius: .25rem;
transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
outline:none;
}// end

& > .post-summary-continue:hover {
color: $color-smokey;
background-color: $color-eggshell;
text-decoration:none;
}

& > .post-summary-continue:visited, & > a.post-summary-continue:active, & > a.post-summary-continue:focus {
border:none;
outline:none;
}

& > .post-summary-data {
grid-column: 3;
grid-row: 6;
}

}//end post-summary-grid

.mobile-iframe {
width: 100%;
height: 0;
padding-bottom: 56.2%;
position: relative;
}

.mobile-iframe iframe {
position: absolute;
width: 100%;
height: 100%;
}

.post h5 {
margin-bottom: 1rem;
}

.ul-news {
display: inline-block;
}

.img-zoom {
display: block;
margin: auto;
max-width: 100%;
}

.aspectRatioPlaceholder .img-zoom {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .img-zoom {
cursor: pointer;
cursor: -webkit-zoom-in;
backface-visibility: hidden;
}

.zoom-effect .zoomImg,
.zoom-effect .zoomImg-wrap {
position: relative;
z-index: 900;
transition: all 300ms;
}

.zoom-effect .zoomImg-wrap--absolute {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .zoomImg {
cursor: pointer;
cursor: -webkit-zoom-out;
}

.zoom-effect .zoomOverlay.show {
opacity: 1;
}

.zoom-effect .zoomOverlay {
z-index: 800;
background: #fff;
position: absolute;
transition: opacity 300ms;
opacity: 0;
cursor: pointer;
cursor: -webkit-zoom-out;
}

img-end, p {
margin-block-end: 0;
margin-block-start: 0;
}

.inline-image {
float: right;
margin-left: 10px;
}
.inline-image img {
border-radius: 10px;
max-width: 100%;
height: auto;
}

//share icons
.share-icons {
font-size: $med;
}

.share-icons a {
color: $color-share-egg;
}

.share-icons .fa-inverse {
color:$color-share;
}

//article on search page summary
.thumb-search {
height: 5.625rem;
padding: 0.25rem;
border-radius: 0.8rem;
width: 100%;
object-fit: contain;
}


.thumb-img {
@include responsive(small-phone) {
height: 7.5rem;
}

@include responsive(phone) {
height: 7.5rem;
}

@include responsive(tablet) {
height: 5.625rem;
}

@include responsive(desktop) {
height: 300px;
}
}//end search image
